// 熱門特展 手機滿版
.container-sm-none{
    @media (max-width: 376px) {
        width: 100%;
        padding: 0;
        margin: 0;
      }
}



.btn{
    padding: 10px 0;
}
.fz-36{
    font-size: 36px;
}
.fz-40{
    font-size: 40px;
    font-family: 'Roboto'; //甘安捏？
}
.yk-nav{
    padding: 0 85px;
}
.bg-setting{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.banner-img{
    height: 480px;
    background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/exhibition-week6/ricardo-gomez-angel-UrOZTfXT0h0-unsplash%20(1).jpg?raw=true");
  }
.footer-img{
    height: 400px;
    background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/exhibition-week6/wladislaw-peljuchno-Y_f1aHpR1tc-unsplash.jpg?raw=true");
    padding-bottom: 36px; /*HTML那邊推不動＝＝*/

}
  .nav-scroller{
    height: 2.75rem; /* 隱藏下方捲軸 */
    overflow-y: hidden;
    border-bottom: 1px solid #707070;
    .nav{
        display: flex;
        justify-content: center;
        flex-wrap: nowrap;
        padding-bottom: 1rem; /* 隱藏下方捲軸 */
        overflow-x: scroll;
        white-space: nowrap;
        a:hover{
            color: $secondary;
            border-bottom: 3px solid $secondary;
        }
      }
  }


  .swiper-container {
    max-width: 100%;
    height: 100%;
  }
  
  .swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .swiper-slide img {
    width: 100%;
    height: 350px;
    object-fit: cover;
  }

  .card-2{
    height: 100%;
    background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/exhibition-week6/michel-catalisano--a0D9LpcKV8-unsplash.jpg?raw=true");
  }
  .card-3{
    height: 100%;
    background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/exhibition-week6/moralis-tsai-8xsktFRw28I-unsplash.jpg?raw=true");
  }

  .liveStream-img{
      height: 482px;
      background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/exhibition-week6/people2.png?raw=true");
  }
  .liveStream-text{
      color: $white;
      background: #190014CC;
  }


  @include media-breakpoint-up(md) {
    //detail頁面的購票
    .w-md-50 {
      width: 50%;
    }
  }
  @include media-breakpoint-up(lg) {
    .mb-xl-4{
        margin-bottom: 16px;
    }
    .mb-xl-20{
        margin-bottom: 80px;
    }
    .mb-xl-25{
        margin-bottom: 100px;
    }
  }

 

//modal
.modal.right .modal-dialog{
    position: fixed;
    top: 0;
    right:0;
    width: 100%;
    height: 100%;
    margin: 0;
    transform: translate3d(0%, 0, 0); /*這是蝦咪*/
  }
  .modal.right .modal-content{   /*這一段不太懂*/
    height: 100%;
    overflow-y: auto;
  }
  .modal{
    &-content{
      border-radius: 0;
      border: none;
    }
    &-header{
      border-bottom: none;
    }
    &-body{
        padding-top: 0;
      .card{
        min-width: 540px; /*哪裡有card?*/
      }
    }
    &-footer{
      border-top: none;
      display: flex;
      justify-content: space-between;
    }
  }


  //進度條
  .timeline-section{
    position: relative;
    margin: 30px 0;
    font-size: 14px;
    &::before{
        content: "";
        position: absolute;
        height: 3px;
        left: 0;
        right: 0;
        background-color: #E31277;}
    p{
        margin-top: 8px;
    }
  }
    //實心
  .timeline-pointer{
    position: relative;
    display: flex;
    justify-content: center;
    &::after{
        content:"";
        position: absolute;
        left: 50%;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        transform: translate(-50%,-25%);
        background-color: #E31277;
      }
  }
  //空心
  .timeline-pointer-border{
    position: relative;
    display: flex;
    justify-content: center;
    &::after{
        content:"";
        position: absolute;
        left: 50%;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        transform: translate(-50%,-25%);
        background-color: #ffffff;
        border: 1px solid #E31277;
      }
  }

  //訂單資訊 
  .yk-input-border{
      border: 1px solid #CED4DA;
  }


  .material-icons-check{
      font-size: 44px;
      text-align: center;
  }